import { FC } from "react";
import { events } from "@/constants/events";
import { Calendar, MapPin } from "lucide-react";

interface EventDetailsPageProps {
    params: {
        eventId: string;
    };
}

const EventDetailsPage: FC<EventDetailsPageProps> = ({ params }) => {
    const event = events.find((e) => e.id === params.eventId);

    if (!event) {
        return <div className="p-10 text-center text-red-600">Event not found</div>;
    }

    return (
        <div className="p-6 max-w-5xl mx-auto">
            <div className="flex flex-col lg:flex-row gap-6">
                <img
                    src={event.image}
                    alt={event.title}
                    className="w-full lg:w-1/2 rounded-xl shadow-md"
                />
                <div>
                    <h1 className="text-3xl font-bold mb-4">{event.title}</h1>
                    <p className="text-gray-600 mb-2 flex items-center gap-2">
                        <MapPin className="w-4 h-4" /> {event.location}
                    </p>
                    <p className="text-gray-600 mb-2 flex items-center gap-2">
                        <Calendar className="w-4 h-4" /> {event.date}
                    </p>
                    <p className="mt-4">{event.description}</p>
                    <div className="mt-6">
                        <h2 className="text-xl font-semibold mb-2">What to Expect:</h2>
                        <ul className="list-disc list-inside space-y-1 text-gray-700">
                            {event.details.map((item, index) => (
                                <li key={index}>• {item}</li>
                            ))}
                        </ul>
                    </div>
                    <div className="mt-6">
                        <button className="px-4 py-2 bg-pink-600 text-white rounded mr-4">Book Event</button>
                        <button className="px-4 py-2 border border-pink-600 text-pink-600 rounded">Learn More</button>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default EventDetailsPage;